.o-table {
    border-collapse: collapse;
}

.o-table th,
.o-table td {
    border: 1px solid black;
}

.o-table td {
    padding: 2px 6px;
    text-align: center;
}

.o-table tr:hover {
    background-color: #f1f1f1;
}

.o-table-label {
    white-space: nowrap;
    text-align: left !important;
}